@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    outline: none;
    border-style:none;
    letter-spacing: 2px;
}

html,
body {
    width: 100%;
    height: 100%;
}

#root{
    display:flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

body {
    font-family: 'Raleway', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #fdf8e7;
}

main{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
}
.faqTitle{
     margin-bottom: 10px;
}

.question__list{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 500px;
    width: 750px;
}

.question{
    width: 750px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    padding: 5px;
    border-bottom: 1px solid black;
}

.showMore{
   width: 25px;
   height: 25px;
   background-color: transparent;
   cursor: pointer;
}

.answer{
    padding: 5px 10px;
    width: 750px;
    white-space: pre-line;
    text-align: justify;
}
.section1{
    width: 800px;
    background-color: #e0ddd1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.home__form{
    height: 70px;
    width: 750px;
    background-color: #e0ddd1;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid white;
}

.search__input{
    width: 350px;
    height: 40px;
    padding:0 10px;
    border: none;
    border-style: none;
    border-radius: 10px;
    font-size: 16px;
}

.location__input{
    width: 200px;
    height: 40px;
    padding:0 10px;
    border: none;
    border-style: none;
    border-radius: 10px;
    font-size: 16px;
}


.search__img{
    width:20px;
    height:20px;
}

.search__btn{
    border:none;
    border-style: none;
    border-radius: 10px;
    background-color: white;
    width: 70px;
    height: 40px;
    transition: .4s all;
    cursor: pointer;
}

.search__btn:hover{
    border: 2px solid white;
    box-shadow: 0px 0px 5px 5px #FFFFFF;;
    background-color: #e0ddd1;
}

.vline{
width: 1px;
height: 60%;
background-color: white;
}

.p__cat{
    height: 110px;
    width: 800px;
    font-size: 46px;
    padding: 50px, 0;
    background-color: #e0ddd1;
    text-align: center;
    padding-top: 50px;
}
.userInfo{
    display: flex;
    margin-left: 270px;
}

.userImg{
    width: 100px;
    height: 100px;
    padding-right: 30px;
}

.userName{
    font-size: 18px;
}

.userBio{
    width: 50%;
    padding-top: 20px;
    font-size: 14px;
}

.userContact{
    display: flex;
    width: 100%;
}

.contactText{
    padding-right: 30px;
    padding-top: 20px;
    font-size: 14px;
}

.booksTitle{
    font-size: 18px;
    margin-top: 10px;
}

.userBooks{
    margin-top: 20px;
    width: 800px;
    height: 400px;
    background-color: white;
    overflow: scroll;
}

.Books{
    width: 700px;
    height: 200px;
    margin: 40px;
    border-radius: 10px;
    background-color: #E0DDD1;
}
.signin__form{
    width: 530px;
    height: 110px;
    background-color: rgba(224, 221, 209, .5) ;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.textBox{
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.input{
    height: 30px;
    width: 225px;
    box-sizing: border-box;
    background-color: rgba(224, 221, 209, 0);
    border-bottom: black 1px solid;
    padding-left: 10px;
    font-size: 14px;
}

.signin__button{
    width: 120px;   
    height: 30px;
    background-color: #ded9cc;
    border-style: none;
    margin-bottom: 12px;
    font-size: 14px;
}

.signin__button:hover{
    background-color: #fdf8e7;
    cursor: pointer;
}

.error{
    height: 30px;
    width: 225px;
    box-sizing: border-box;
    background-color: rgba(224, 221, 209, 0);
    border-style: none;
    padding-left: 10px;
    font-size: 14px;
    border-bottom: red 1px solid;
}
.signup__form{
    width: 320px;
    height: 320px;
    background-color: rgba(224, 221, 209, .5) ;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.input{
    height: 30px;
    width: 250px;
    box-sizing: border-box;
    background-color: rgba(224, 221, 209, 0);
    border-bottom: black 1px solid;
    padding-left: 10px;
    margin-top: 10px;
    font-size: 14px;
}

.signup__button{
    width: 120px;   
    height: 30px;
    background-color: #ded9cc;
    border-style: none;
    font-size: 14px;
    margin-top: 30px;
}

.signup__button:hover{
    background-color: #fdf8e7;
    cursor: pointer;
}

.error{
    height: 30px;
    width: 250px;
    box-sizing: border-box;
    background-color: rgba(224, 221, 209, 0);
    padding-left: 10px;
    margin-top: 10px;
    font-size: 14px;
    border-bottom: red 1px solid;
}
header{
    height: 130px;
    width: 100%;
}

.logo{
    height: 130px;
    width: 178px;
    margin-left: 156px;
}
footer{
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: rgba(224, 221, 209, .5) ;
    font-size: 16px;
}

.nav{
    display: flex;
}

.nav__item{
    color: black;
    text-decoration: none;
    cursor: pointer;
    list-style: none;
}

li{
    list-style: none;
    padding-left: 10px;
    padding-right: 10px;
    border-right: black 1px solid;
}

li:nth-last-of-type(1){
    border: none;
}
